*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 14px;
}

 body {  
    margin: 0;
    font-family: Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
    font-size: .875rem;
    font-weight: 400;
    line-height: 1.5;
    color: #343a40;
    text-align: left;
    background-color: #f5f9fc;
}

a {
  text-decoration: none;
  color: #fff;
  
  &:hover {
    color: #fff;
    cursor: pointer;
  }
}

button {
  padding: .4rem .8rem;
 
  color: #fff;
  font-weight: bold;
  text-align: center;
   
  user-select: none;
  font-size: 1.1rem;
  line-height: 1.5rem;
   
  border: none;
  outline: none;
  border-radius: .25rem;
  
  cursor: pointer;
}

.wrrapper {
  display: flex;
 
  height: 100vh;
  overflow: hidden;
  width: 100%;
}

/* ======= Section - navbar =======*/
.navbar-custom {
  display: flex;

  background-color: #38414a;
  box-shadow: 0 0 25px 0 rgba(154,161,171,.15);

  padding: 0.5rem 0;
  padding-left: 3rem;

  position: fixed;
  left: 0;
  right: 0;
  height: 50px;
  z-index: 100;

}

  .logo img{
    width: 180px; 
    height: 100%;
  }

  .button-new-appeal {
    margin-left: 4rem;

    border: 1px solid #4631ff;
    background-color: #4631ff;
   
    &:hover {
      background-color: #4c1fee;
      border-color: #4c1fee;
    
      transition-duration: 0.2s;
    }

    & span{
      font-weight: normal;

      margin-left: 0.4rem;
      vertical-align: middle;
      font-size: 2.4rem;
    }

    &:active {
      background-color: #8275ee;
      box-shadow: 0 0 2px 2px rgb(71, 51, 250);
      transition-duration: 0.2s;
    }

  }



/* ======= Main =======*/
.page-main {
  width: 100%;
  height: 100vh;

  background-color: #f3f7fa;
}


/* ======= Filter-nav =======*/
.left-filter-menu {
  position: fixed;

  width: 260px;
  height: calc(100%-50px);

  margin-top: 50px;
  padding: 10px 10px;

  overflow-y: hidden;
  background-color: #FFF;
  box-shadow: 0px 1px 3px rgba(63, 63, 68, 0.15), 1px -1px 4px rgba(63, 63, 68, 0.04);
}

.filter-list {
  padding: 0;
  list-style: none;

  font-size: 16px;
}

.filter-list-item {
  margin-top: 20px; 

  & label {
    font-weight: bold;
  }
}

.select2 {
  width: 100%;
  max-width: 240px;
}

.btn-wrapper {
  display: flex;

  margin-top: 30px;
}

.btn-filter {
  position: relative;
  padding: 0.5rem 0.8rem;

  font-size: 1rem;
}

.button-decline {
  margin-right: auto;

  background-color: #a50404d4;
  
  &:active {
    background-color: #f34646d4;
    box-shadow: 0 0 2px 2px rgb(243, 27, 27);
  } 
}

.button-apply {
  background-color: #0c740c;

  &:active {
    background-color: #32b632;
    box-shadow: 0 0 2px 2px rgb(13, 151, 54);
  } 
}
/* ======= Content-page =======*/
.content-page {
  height: calc(100vh-50px);

  margin-left: 260px;
  margin-top: 50px;
  padding: 20px;

  overflow: hidden;
}

.content {
  height: 100%;

  padding-bottom: 3rem;
  background-color: #FFF;
  box-shadow: 0px 1px 3px rgba(63, 63, 68, 0.15), 1px -1px 4px rgba(63, 63, 68, 0.04);
  border-radius: 3px;
}

.data-table {
  display: block;
  width: 100%;
  height: 100%;

  padding: 1rem;

  &-body {
    height: calc(100% - 134px);

    overflow-x: auto;
  }
}


.table-head thead tr th {
    position: sticky;
    top: 0;
    padding-top: 0;
    text-align: left;
}


.table {
  position: relative;
  
  width: 100%;

  border-collapse: collapse;
  table-layout: fixed;

  font-size: 14px;
  vertical-align: middle;
  text-align: center;
  overflow: hidden;
  

  & thead tr th{

    padding: 18px 4px;
    border-bottom: 1px solid #848484;

    font-size: 18px;
    
    &:last-child {
      text-align: center;
    }

    & > * {
      display: block;
    }

    &:last-child {
      max-width: 100px;
    }
  }

  & tbody tr td{
    padding: 8px 10px;
    border-bottom: 1px solid #848484;
    text-align: left;

    & .date {
      font-size: 12px;
    }

    & .item-ref {
      color: #343a40;

      text-decoration: underline;

      &:hover {
        color: #00bdec;
      }

      &:active {
        color: #c70000;
      }
    }
    
    & > * {
      display: block;
    }
  }
}

.input-search {
  width: 100%;

  
  margin: 0 auto;
  margin-bottom: 5px;
  padding: 4px 2px;
  padding-left: 8px;

  border: none;
  border-radius: 5px;

  font-size: 1rem;
  line-height: 2rem;

  background-color: rgb(249, 249, 249);
}

.days {
  font-size: 16px;
  text-align: center;
}

.blu{
  color: rgb(74, 74, 231) !important;
  font-weight: bold;
}

.grn{
  color: green !important;
  font-weight: bold;
}

.rd{
  color: rgb(223, 17, 17) !important;
  font-weight: bold;
}